import { useState,createContext } from "react";

export const MyFile = createContext(null);
export const Filter = createContext(null);
export function Context({children}) {
    const [dogDetails,setDogDetails] = useState({});
    return(
        <MyFile.Provider value={{dogDetails,setDogDetails}}>
            {children}
        </MyFile.Provider>
    );  
}
export function ContextForFilter({children}) {
    const [mainKeyword,setMainKeyword] = useState("All");
    return(
        <Filter.Provider value={{mainKeyword,setMainKeyword}}>
            {children}
        </Filter.Provider>
    )};  
