import React, { useState, useContext , useRef } from "react";
import Card from "../Card/Card";
import "./Canvas.css";
import Data from "../../data.json";
import Masonry from "react-masonry-css";
import { Filter } from "../../Context";

function Canvas() {
  const { mainKeyword } = useContext(Filter);
  const count = useRef(0);
  const breakpointColumnsObj = {
    default: 4,
    900: 2,
  };
  const setRef=()=>{
    count.current=0;
  }
  return (
    <div className="canvas">
      <Masonry
        breakpointCols={breakpointColumnsObj}
        className="my-masonry-grid"
        columnClassName="my-masonry-grid_column"
      >{setRef()}
        {
        Data.data.map((current) => {
          if (mainKeyword == "All" || mainKeyword == "all") {
            return (
              <Card 
                key={current["breed"]}
                src={current.images[0]}
                name={current["breed"]}
                all={current}
              />
            );
          } else if (current["breed"].toLowerCase() == mainKeyword) {
            return (
              <Card
                key={current["breed"]}
                src={current.images[0]}
                name={current["breed"]}
                all={current}
              />
            );
          }
          else {
            count.current++;
            if(count.current==Data.data.length){
              return(<Card key={"0"}  name="Search Result Not Found" />)
            }
            
            
          } 
        })}
      </Masonry>
    </div>
  );
}

export default Canvas;
