import React from "react";
import "./Product.css";
import Carousel from "react-elastic-carousel";
import Contact from "../../Components/Contact/Contact";
import Header from "../../Components/Header/Header";

function Product() {
  const dogDetails = JSON.parse(localStorage.getItem("details"));
  const gmail = "mailto:" + dogDetails.email;
  const whatsapp = "https://wa.me/" + dogDetails.whatsapp;
  return (
    <div className="product-page">
      <Header />
      <div className="product">
        <div className="m-slider">
          <Carousel itemsToShow={1}>
            {dogDetails.images.map((item) => (
              <div className="image-wrap">
                <img src={item} alt="" />
              </div>
            ))}
          </Carousel>
        </div>
        <div className="details">
          <div className="breed">
            <h2 id="breed">{dogDetails.breed}</h2>
          </div>
          <p id="details">{dogDetails.details}</p>
          <h1 id="price"> {dogDetails.price}</h1>
        </div>
        <div className="contact">
          <a href={whatsapp}>
            <div id="whatsapp">
              <span>Whatsapp</span>
              <ion-icon name="logo-whatsapp"></ion-icon>
            </div>
          </a>
          <a href={gmail}>
            <div id="email">
              <span>Email</span>
              <ion-icon name="mail-outline"></ion-icon>
            </div>
          </a>
        </div>
      </div>
      <Contact />
    </div>
  );
}

export default Product;
