import React, { useContext ,useEffect } from "react";
import { Link } from "react-router-dom";
import "./Card.css";
import {MyFile} from "../../Context";

function Card(props) {
  const {setDogDetails}=useContext(MyFile);
  const getCardValue = () => {
    const data=props.all;
    setDogDetails(props.all);
    localStorage.setItem("details", JSON.stringify(data))
  };

  return (
    <Link to="/product" style={{ textDecoration: "none" }}>
      <div className="card" onClick={getCardValue}>
        <img src={props.src} alt="" />
        <h3 className="desc">{props.name}</h3>
      </div>
    </Link>
  );
}

export default Card;
